@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body{margin:0;width:100%;font-family:-apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI", "Oxygen",  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",  sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New",  monospace}.layout{display:block;background-color:#efefef;color:#1ca3a8;padding:10vh 20vw 0vh 20vw}@media (max-width: 767px){.layout{padding:0}}.main-container{display:block;height:100vh;overflow:hidden !important}.main-container .navbar{width:100%;height:70px;overflow:hidden}.main-container .navbar .title{font-family:"Pacifico", cursive;color:#1ca3a8}.main-container .content{position:relative;top:30vh;left:0vw;color:black;width:100vw;z-index:2;padding:0px 5vw}.main-container .content h1{color:#1ca3a8}@media (max-width: 767px){.main-container .content{top:25vh;padding:2rem 10px}}.main-container .icons{position:absolute;bottom:20px;left:15vw;z-index:5}.main-container .icons img{width:30px;height:30px;margin-right:15px;cursor:pointer;z-index:2}@media (max-width: 767px){.main-container .icons{left:50%;transform:translate(-50%, 0%)}}@media (max-width: 767px){.main-container{padding:0px}.main-container .content{top:15vh}.main-container .main-image{opacity:0.6}}.section-light{display:block;overflow:hidden;padding-bottom:10rem}@media (max-width: 767px){.section-light{padding:2rem}}.time-span{text-align:right;padding-top:10px}@media (max-width: 767px){.time-span{text-align:left;padding-top:0px}}.tech-img{width:100%;height:150px;object-fit:contain}@media (max-width: 767px){.tech-img{height:100px}}.text-link{color:#ff3b3f;cursor:pointer}footer{width:100%;height:25vh;text-align:center;background-color:#1ca3a8;padding:0vh 20vw}footer .about{margin-top:10vh;display:inline-block;width:50% !important;text-align:left}footer .about h1{font-family:"Pacifico", cursive;color:#efefef}footer .connect{margin-top:7vh;display:inline-block;width:50% !important;vertical-align:top;text-align:center;color:#efefef}footer .connect svg{color:#efefef;width:40px;height:40px;margin:0px 10px;cursor:pointer}@media (max-width: 767px){footer{height:40vh}footer .about,footer .connect{display:block;width:100% !important;text-align:center;margin-top:0vh;padding-top:5vh}}

