.section-light {
  display: block;
  overflow: hidden;
  padding-bottom: 10rem;

  @include respond-below(sm) {
    padding: 2rem;
  }
}

.time-span {
  text-align: right;
  padding-top: 10px;

  @include respond-below(sm) {
    text-align: left;
    padding-top: 0px;
  }
}

.tech-img {
  width: 100%;
  height: 150px;
  object-fit: contain;

  @include respond-below(sm) {
    height: 100px;
  }
}

.text-link {
  color: $theme-light-secondary;
  cursor: pointer;
}

footer {
  width: 100%;
  height: 25vh;
  text-align: center;
  background-color: $theme-light-text;
  padding: 0vh 20vw;

  .about {
    margin-top: 10vh;
    display: inline-block;
    width: 50% !important;
    text-align: left;

    h1 {
      font-family: "Pacifico", cursive;
      color: $theme-light-color;
    }
  }

  .connect {
    margin-top: 7vh;
    display: inline-block;
    width: 50% !important;
    vertical-align: top;
    text-align: center;
    color: $theme-light-color;

    svg {
      color: $theme-light-color;
      width: 40px;
      height: 40px;
      margin: 0px 10px;
      cursor: pointer;
    }
  }

  @include respond-below(sm) {
    height: 40vh;

    .about,
    .connect {
      display: block;
      width: 100% !important;
      text-align: center;
      margin-top: 0vh;
      padding-top: 5vh;
    }
  }
}
